@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Title-style {
  /*position: absolute;*/
  width: 0%;
  height: 0%;
  left: 0%;
  top:0%;

  font-family: 'Comfortaa';
  font-style: normal;
  font-weight: 300;
  font-size: 48px;
  line-height: 54px;
  align-items: center;
  text-align: center;

  color: #000000;
}

.Add-note {
  /*position: absolute;*/
  width: 156px;
  height: 0%;
  left: 0%;
  top:0%;
  bottom: 10.00%;

  background: linear-gradient(180deg, #FFFFFF 0%, #f4f4f4 100%);
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 20px;
}

.Add-note-text {
  /*position: absolute;*/
  left: 23.72%;
  right: 23.72%;
  top: 20.31%;
  bottom: 21.88%;

  font-family: 'Comfortaa';
  font-style: normal;
  font-weight: normal;
  font-size: 33px;
  line-height: 37px;
/* identical to box height */
  align-items: center;
  text-align: center;
  color: #000000;
  margin: 10px;
}

.Note-mask {
  /*position: absolute;*/
  width: 100%;
  height: 896px;
  left: 10%;
  top: 10%;

  background: linear-gradient(180deg, #FAFAFA 0%, rgba(250, 250, 250, 0) 100%);
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 20px;
}



