.Note-list {
    position: relative;
    /*width: 451px;*/
    height: 896px;
    left: 0%;
    top: 10%;
    /*margin-left: 2em;
    margin-right: 2em;*/
}

.Note-list-frame {
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    
    background: linear-gradient(180deg, #FAFAFA 0%, rgba(255, 255, 255, 0) 100%);
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 20px; 
    overflow-y: scroll;
}

.Note-item {
    margin: 2%;
    height: 20%;
    min-height: 180px;

    background: linear-gradient(180deg, #FAFAFA 0%, rgba(255, 255, 255, 0) 100%);
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;
    overflow-y: scroll;
}

.Head-row, .Date-row, .Tag-row {
    border: none;
    top: 10%;
    right: 50%;
    height: 30%;

    font-family: 'Comfortaa';
    font-style: normal;
    font-size: 24px;
    line-height: 27px;
    display: flex;
    align-items: center;
    text-align: left;
    margin: 0px;
    padding: 0px;
}

.Head-row {
    font-weight: bold;
    overflow-x: hidden;
}

.Tag-box{
    margin: 2px;
    padding: 2px;
    display: inline-block;
    font-size: 9px !important;
    border: 1px solid #000000;
    box-sizing: border-box;
}

.Delete-note {
    width: 10%;
    height: 20%;
    background: linear-gradient(180deg, #FFFFFF 0%, #f4f4f4 100%);
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;
    float: right;
    color: #000000;
    
    font-family: 'Comfortaa';
    font-style: normal;
    font-size: 1.25vw;
    line-height: 27px;
    align-items: center;
    text-align: center;
}

.Search {
    /*position: absolute;*/
    height: 64px;
    left: .5%;
    top: 0%;
    width: 690px;
    /*height: 64px;
    left: 518px;
    top: 23px;
    width: 690px;*/

    background: #FFFFFF;
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;
}

.Search-input {
    position: absolute;
    width: 90%;
    left: 5.6%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    border: none;

    font-family: 'Comfortaa';
    font-style: normal;
    font-weight: 300;
    font-size: 34px;
    line-height: 38px;
    display: flex;
    align-items: center;
    text-align: left;
}

.Search-input:focus{
    outline: none;
}



.Note {
    /*position: absolute;*/
    width: 100%;
    height: 896px;
    left: 10%;
    top: 10%;

    background: linear-gradient(180deg, #FAFAFA 0%, rgba(250, 250, 250, 0) 100%);
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;
}

.Note-title, .Note-body, .Note-tags {
    margin: 10px;
}

.Note-title {
    position: absolute;
    width: 90%;
    left: 2.33%;
    right: 0%;
    top: 1.9%;
    bottom: 86.83%;
    

    font-family: 'Comfortaa';
    font-style: normal;
    font-weight: normal;
    font-size: 2.5vw;
    line-height: 4.54px;
    align-items: center;
    background: transparent;

    color: #000000;
    border: none;
}

.Note-body {
    position: absolute;
    /*width: 871px;*/
    width: 90%;
    left: 2.33%;
    right: 0%;
    top: 16.85%;
    bottom: 10.94%;
    
    font-family: Comfortaa;
    font-style: normal;
    font-weight: normal;
    font-size: 1.875vw;
    line-height: 40px;
    background: transparent;
    
    color: #000000;
    border: none;
}

.Note-tags {
    position: absolute;
    left: 2.33%;
    right: 65.82%;
    top: 92.86%;
    bottom: 1.9%;
    
    font-family: Comfortaa;
    font-style: normal;
    font-weight: normal;
    font-size: 1.25vw;
    line-height: 27px;
    
    align-items: center;
    background: transparent;
    
    color: #000000;
    border: none;
}

@media (max-width: 600px){
    .Note-title{
        font-size: 10.5vw;
    }

    .Note-body{
        font-size: 7.75vw;
    }

    .Note-tags {
        font-size: 5.25vw;
    }
}

.Note-body, .Note-tags, .Note-title:focus{
    outline: #C4C4C4;
}

.dash {
    border: 1px solid #c0c0c0;
}

.Up {
    position: relative;
    left: 0%;
    top: 14%;
}

.Down {
    position: relative;
    left: 0%;
    top: 90%;
}

.full{
    width: 100vw;
    display: flex; 
    flex:1 1;
}
